import { Menu, MenuButton, MenuItem, MenuItems } from '@headlessui/react';
import useGenerateSearchParams from '@lib/hooks/useGenerateSearchParams';
import { Link } from '@remix-run/react';

export type ActionMenuLinkProps = {
  text: string;
  url: {pathname: string; searchParams?: Record<string, string>;} | string;
  type?: 'default' | 'danger'
}

export function ActionMenuLink({text, url, type =  'default'}: ActionMenuLinkProps) {
  const searchParams = useGenerateSearchParams({keysToRemove: 'type', keysToAdd: typeof url !== 'string' ? url.searchParams : undefined});
  const dangerClass = ' text-red-600 dark:text-gray-300 hover:text-slate-50 hover:bg-red-500 bg-white';
  const defaultClass = ' text-gray-600 dark:text-gray-300 hover:bg-gray-200 dark:hover:bg-gray-700 dark:hover:text-white';
  return (
    <MenuItem>
      <Link
        className={`block px-4 py-3 text-sm capitalize transition-colors duration-300 transform${type === 'danger' ? dangerClass : defaultClass}`}
        to={{
          pathname: typeof url === 'string' ? url : url.pathname,
          search: searchParams,
        }}>
        {text}
      </Link>
    </MenuItem>
  );
}

export default function ActionMenu({
  children,
}: React.PropsWithChildren) {
  return (
    <Menu as="div" className="relative inline-block">
      <>
        <MenuButton
          className={`inline-flex px-3 py-1 data-[open]:bg-zinc-600 data-[open]:text-slate-50 text-gray-700 bg-zinc-300 text-sm border border-transparent rounded-md dark:text-white focus:border-blue-500 focus:ring-opacity-40 focus:ring focus:outline-none hover:bg-zinc-600 hover:text-slate-50`}>
            Actions
          <svg
            className="w-5 h-5 text-gray-800 dark:text-white ml-1"
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 20 20"
            fill="currentColor">
            <path
              fillRule="evenodd"
              d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z"
              clipRule="evenodd"/>
          </svg>
        </MenuButton>
        <MenuItems className="absolute z-50 text-left right-0 mt-2 w-56 origin-top-right divide-y divide-gray-100 rounded-md bg-white shadow-lg ring-1 ring-black/5 focus:outline-none">
          {children}
        </MenuItems>
      </>
    </Menu>
  );
}
